 .members-search-input{
    input{
        width: 100%;
        padding: 10px;
    }
    .results{
        a{
            font-size: 15px;
            text-decoration: none;
            padding: 5px 0 0 5px;
            display: block;
        }

        .error{
            font-size: 14px; 
        }

        .item{
            background-color: #f4f3f3;
            width: 100%;
            font-size: 16px;
            display: flex;
            align-items: center;
            gap: 10px;
            margin: 5px 0;

            img{
                width: 32px;
                height: 32px;
                object-fit: cover;
                border-radius: 50%;
            }

            span {
                font-size: 14px;
                vertical-align: top;
                padding: 5px;
                display: inline-block;
            }
        }
    }
 }